.link, .link:visited {
    text-decoration: none;
    position: relative;
    color:white;
  }
  .link:after, .link:visited:after {
    content: '';
    height: 3px;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0%;
    background: rgb(229, 21, 53);
    transition: 0.2s;
  }
  .link:hover:after, .link:visited:hover:after {
    width: 100%;
  }
  .link.block, .link:visited.block {
    display: block;
    padding: 0.5em;
  }
  
